<template>
  <g-body-layout
    :pageIndex="pageNumber"
    :pageSize="pageSize"
    :total="total"
    :filterText.sync="filterText"
    @pageIndexChange="pageIndexChange"
    @pageSizeChange="pageSizeChange"
    @suffixSearchCallback="onFilterReady"
  >
    <!-- 按钮组插槽 -->
    <template #btnGroup>
      <g-btn-group :buttonLists="btnList" @btnCallBack="btnGroupClick">
      </g-btn-group>
    </template>
    <!-- g-ndo-table插槽 -->
    <template #table>
      <g-ndo-table
        :id="id"
        :handName="'时间窗管理'"
        :dataList="dataList"
        :isTableLoading="loading"
        :total="total"
        @rowClick="rowClick"
      ></g-ndo-table>
    </template>
    <!-- 详细内容插槽 -->
    <template #content>
      <create-or-edit-time-window
        :ndoEditParams="ndoEditParams"
        :refreshTag="refreshTag"
        :saveTag="saveTag"
        :resetTag="resetTag"
        @childRefresh="childRefresh($event)"
        @rowClick="rowClick($event)"
        v-if="isShowEdit"
      ></create-or-edit-time-window>
      <g-empty v-else> </g-empty>
    </template>
  </g-body-layout>
</template>

<script>
import CreateOrEditTimeWindow from "./create-or-edit-time-window/create-or-edit-time-window.vue";
import GCopyNdo from "@/components/g-copy-ndo/g-copy-ndo.vue";
import { GNdoPageBase } from "@/utils";

export default {
  name: "TimeWindow",
  mixins: [GNdoPageBase],
  components: {
    CreateOrEditTimeWindow,
  },

  data() {
    return {
      isTableLoading: false,
      filterText: "",
    };
  },

  mounted() {},
  created() {
    this.getBtnList();
  },
  methods: {
    getBtnList() {
      // 获取按钮列表
      this.btnList = [
        {
          text: this.AppConst.button.create,
          icon: this.AppConst.icon.create,
          eventName: this.AppConst.button.create,
          display: true,
        },
        {
          text: this.AppConst.button.copy,
          icon: this.AppConst.icon.copy,
          eventName: this.AppConst.button.copy,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.delete,
          icon: this.AppConst.icon.delete,
          eventName: this.AppConst.button.delete,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.refresh,
          icon: this.AppConst.icon.refresh,
          eventName: this.AppConst.button.refresh,
          right: true,
          display: this.isShowEdit,
        },
        {
          text: this.AppConst.button.save,
          icon: this.AppConst.icon.save,
          eventName: this.AppConst.button.save,
          right: true,
          type: "primary",
          display: this.isShowEdit,
        },
      ];
    },
    copy() {
      this.createModal(
        { title: "复制", width: "600px" },
        {
          component: GCopyNdo,
          props: {
            entity: {
              copyValue: this.dataList[0].name,
              value: this.dataList[0].name,
            },
          },
          on: {
            success: (res) => {
              console.log(res);
              // 手动延时
              this.loading = true;
              setTimeout(() => {
                this.loading = false;
                this.copySuccess(res.value, res.copyValue);
                this.refreshGoFirstPage();
              }, 500);
            },
          },
        }
      );
    },
    delete() {
      // 删除确认
      this.$confirm({
        title: "确认删除",
        content: "确认删除该时间窗吗？删除后无法恢复。",
        onOk: () => {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            // 消息提示
            this.deleteSuccess();
            // 刷新
            this.refresh();
          }, 500);
        },
      });
    },
    fetchData(request) {
      this.loading = true;
      setTimeout(() => {
        request.finishedCallback();
        // 填充dataList 示例数据
        let dataList = [];
        const timeWindowTypes = [
          "生产时间窗",
          "质检时间窗",
          "物料供应时间窗",
          "设备维护时间窗",
          "车间作业时间窗",
          "交付时间窗"
        ];
        
        const departments = [
          "生产部",
          "质量部",
          "物料部",
          "设备部",
          "计划部",
          "物流部"
        ];
        
        const statuses = ["已启用", "已禁用", "临时", "已过期"];
        const repeatTypes = ["每天", "每周", "每月", "指定日期", "不重复"];
        
        for (let i = 0; i < 25; i++) {
          const typeIndex = i % 6;
          const departmentIndex = i % 6;
          const statusIndex = i % 4;
          const repeatTypeIndex = i % 5;
          
          // 生成随机的时间段
          const startHour = Math.floor(Math.random() * 12);
          const endHour = startHour + 2 + Math.floor(Math.random() * 10);
          const timeRange = `${startHour.toString().padStart(2, '0')}:00 - ${endHour.toString().padStart(2, '0')}:00`;
          
          // 生成有效日期
          const startDate = new Date(2024, 2, 1 + i % 28);
          const endDate = new Date(2024, 3 + i % 6, 1 + i % 28);
          const effectiveDate = `${startDate.getFullYear()}-${(startDate.getMonth() + 1).toString().padStart(2, '0')}-${startDate.getDate().toString().padStart(2, '0')} 至 ${endDate.getFullYear()}-${(endDate.getMonth() + 1).toString().padStart(2, '0')}-${endDate.getDate().toString().padStart(2, '0')}`;

          dataList[i] = {
            id: i + 1,
            windowCode: `TW${String(i + 1).padStart(4, "0")}`,
            name: `${timeWindowTypes[typeIndex]}${i % 9 + 1}`,
            timeWindowType: timeWindowTypes[typeIndex],
            department: departments[departmentIndex],
            timeRange: timeRange,
            repeatType: repeatTypes[repeatTypeIndex],
            effectiveDate: effectiveDate,
            priority: (i % 5) + 1,
            description: `${timeWindowTypes[typeIndex]}用于${departments[departmentIndex]}的时间窗设置，${repeatTypes[repeatTypeIndex]}重复`,
            status: statuses[statusIndex],
            createdBy: "admin",
            createdTime: "2024-03-19 " + (10 + i % 10) + ":" + (i % 60),
            modifiedBy: "admin",
            modifiedTime: "2024-03-" + (20 + i % 8) + " " + (13 + i % 10) + ":" + (i % 60),
          };
        }

        // filter 过滤
        if (this.filterText) {
          console.log(this.filterText);
          dataList = dataList.filter((item) => {
            return item.windowCode.indexOf(this.filterText) > -1 || 
                   item.name.indexOf(this.filterText) > -1 ||
                   item.timeWindowType.indexOf(this.filterText) > -1 ||
                   item.department.indexOf(this.filterText) > -1 ||
                   item.timeRange.indexOf(this.filterText) > -1 ||
                   item.repeatType.indexOf(this.filterText) > -1 ||
                   item.description.indexOf(this.filterText) > -1;
          });
        }

        request.successCallback({
          items: dataList,
          totalCount: dataList.length * 2,
        });
      }, 1000);
    },
  },
};
</script>

<style lang="scss" scoped></style>